.team{
    padding: 32px;
    box-sizing: border-box;
    width: 100%;
    text-align: center;
    font-size: 32px;
    position: relative;
}

.collaborators{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.team h3{
    font-weight: 400;
    color:#212121 ;
    border-bottom: 4px solid;
    display: inline-block;
    padding-bottom: 8px;

}

.input-color{
    position: absolute;
    right: 50px;
    top: 20px;
}